.landing-1 {

  section {
    @include container--content; }

  section + section {
    margin-top: 150px; }

  &__title-section {
 }    // margin-bottom: 120px

  &__main-title {
    @include gbackground (100%); }

  &__title-wrapper-1 {
    margin-left: 15px; }

  &__title-wrapper-2 {
    box-shadow: 220px 0px 0px -14px #F5F8FF, 415px 0px 0px -14px #F5F8FF, 610px 0px 0px -14px #F5F8FF, 805px 0px 0px -14px #F5F8FF;
    padding: 15px 0;
    color: $white;
    font-size: 4.8rem;
    font-weight: 900;
    text-transform: uppercase; }

  &__title-line {
    padding-right: 15px;
    &::after {
      white-space: break-spaces;
      content: "\A"; } }

  &__title-pitch {
    display: flex;
    margin: 58px 0;
    font-size: 1.8rem; }

  // title-pitch direct children
  &__pitch-topics {
    margin-bottom: 87px; }

  &__pitch-img {
    margin: 0 -200px;
    align-self: flex-end;
    margin-bottom: 120px;
    margin-top: 142px; }

  &__pitch-price {
    margin-bottom: 108px;
    span {
      font-size: 3.6rem;
      line-height: 2.0rem; } }

  &__pitch-button {
    display: flex;
    font-size: 1.8rem;
    width: 396px;
    height: 62px;
    padding: 24px;
    align-items: center;
    justify-content: space-evenly;
    margin-bottom: 156px; }
  // end of title-pitch direct children

  &__price-block {
    @include gbackground (100%);
    color: $white;
    padding: 4px 7px 5px;
    text-transform: none;
    font-weight: 900; }

  &__subtitle {
    @include subtitle(3.6rem);
    font-weight: 800; }

  &__orb-list-1 {
    .orb-list__wrap {
      .orb-list__item {
        width: 175px; }
      div:nth-of-type(2) {
        span {
          margin-left: 30px; } } } }

  &__orb-list-2 {
    .orb-list__item {
      width: 303px; }
    .orb-list__wrap {
      &:first-child {
        div:nth-of-type(3) {
          .orb-list__svg-img {
            margin-left: -20px; } } }
      &:last-child {
        div:nth-of-type(3) {
          .orb-list__svg-img {
            margin-top: 10px; } } } } }

  // &__partners-section // have to change it on HTML for uniform styling of sections?

 }  // &__form-section // have to change it on HTML for uniform styling of sections?
