.catalog {
  @include container--content();

  &__wrapper {
    display: flex;
    flex-flow: row wrap;
    > div {
      &:nth-child(odd) {
        transform: translate(0, -20px); }
      &:nth-child(even) {
        transform: translate(0, 20px); } }
    @include media("<huge") {
      justify-content: center;
      > div {
        &:nth-child(odd) {
          transform: translate(0, 0); }
        &:nth-child(even) {
          transform: translate(0, 0); } } } }


  &__item {
    position: relative;
    display: flex;
    flex-flow: column;
    align-items: center;
    width: 273px;
    height: 305px;
    padding: 20px 30px;
    margin: 10px 10px;
    * {
      z-index: 2; } }

  &__item-border {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%; }

  &__item-code {
    position: absolute;
    top: 23px;
    left: 20px;
    font-size: 1.2rem;
    text-transform: uppercase;
    z-index: 3; }

  &__item-img {
    z-index: 1; }

  &__price-block {
    position: relative;
    align-self: stretch;
    display: flex;
    flex-flow: row wrap;
    padding-top: 35px;
    @include media("<desktop") {
      padding-top: 26px; } }

  &__item-name {
    @include resetmargin;
    @include subtitle(2rem);
    position: absolute;
    bottom: 22px;
    width: 100%;
    flex-basis: 100%;
    text-align: center;
    margin-bottom: 10px;
    @include media("<desktop") {
      font-size: 1.6rem; } }

  &__item-price {
    font-size: 1.8rem;
    &:after {
      content: " p."; } }

  &__item-link {
    margin-left: auto; } }
